import swiperDot from "../swiperDot/swiperDot2.vue"
import utlis from "../../static/js/utlis.js"
export default {
	components: {
		swiperDot
	},
	data() {
		return {
			// 轮播
			list: [{
					content: '内容 A',
					src: "../../static/img/chongdianxiangqing.jpg"
				}
				// ,
				// {
				// 	content: '内容 B',
				// 	src: '../../static/img/xiangqing3x.png'
				// },
				// {
				// 	content: '内容 C',
				// 	src: '../../static/img/cheweifenxiang_icon2x.png'
				// }
			],
			current: 0,
			plie_active: 0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			id: '',
			stationId: '',
			pileInfo: '',
			addr: '',
			esName: '',
			lat: '',
			lng: ''
		}
	},
	onLoad(e) {
		let self = this;
		self.id = e.id;
		self.stationId = e.stationId;
		self.addr = e.addr;
		self.esName = e.esName;
		self.lat = e.lat;
		self.lng = e.lng;
		self.getData()
	},
	methods: {
		openMap: function(event) {
			let self = this;
			uni.getLocation({
				type: 'gcj02', //返回可以用于uni.openLocation的经纬度
				success: function(res) {
					const latitude = res.latitude;
					const longitude = res.longitude;
					console.log(latitude, longitude)
					uni.openLocation({
						latitude: Number(self.lat),
						longitude: Number(self.lng),
						name: self.esName,
						address: self.addr,
						success: function() {
							console.log('success');
						}
					});
				}
			});
		},
		change(e) {
			this.current = e.detail.current;
		},
		changePile(e) {
			if (this.plie_active != e.currentTarget.dataset.index) {
				if (this.plie_active == 0) {
					this.plie_active = 1
				} else {
					this.plie_active = 0
				}
			}
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		showNavi() {
			uni.navigateTo({
				url: '/pages/navi/navi'
			});
		},
		//获取电站详情
		getData() {
			let self = this;
			let data = {
				esCode: self.stationId
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			utlis.http("POST", 'appElectricPileInfoHj', data, function(res) {
				// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appElectricPileInfoHj', data, function(res) {
				if (res.status == "success") {
					self.pileInfo = res.pileInfo
					console.log(res)
					uni.hideLoading()
				} else {
					uni.hideLoading()
				}
			})
		},
	}
}
